<template>
    <div class="answer-hint">
        <!-- После {{ totalHints }} неверных ответов будет дана подсказка. Осталось {{remainedTry}} попытки из {{ totalHints }}. -->
        After three incorrect answers, you will get a hint. You have {{ remainedTry }} attempts out of 3.
    </div>
        <!-- После трех неверных ответов будет дана подсказка. Осталось {{remainedTry}} попытки из 3-х. -->
        
</template>

<script>
export default {
    name: 'AnswerHint',
    props: {
      numberTry: Number,
      totalHints: Number,
      hintText: String
    },
    computed:{
        remainedTry(){
            // return this.totalHints - this.numberTry
            return 3 - this.numberTry
        }        
    }
}
</script>

