<template>
    <div class="percent-wrapper">
      <img src="@/assets/images/cup.svg" alt="cup" width="30" class="percent-image"/>
      <span class="percent-text">
        <!-- Только {{randomPercent}}% пользователей сумели пройти этот сценарий с первого
        раза! -->
        Only {{randomPercent}}% of users were able to complete this scenario on their first try!
      </span>      
    </div>
</template>

<script>
export default {
  name: 'InfoPercent',
  computed: {
    randomPercent(){
        return (Math.random() * (73 - 61) + 61).toFixed(0)
    }    
  }
}
</script>

