<template>

    <li>
        <div class="answer-checkbox">
            <div>
                <div 
                @click="selectCheckbox"
                :class="{'white-checkbox': !isChecked,
                        'purple-checkbox': isChecked,
                        'cursor-pointer': !isDisable
                }"
                class="answer-checkbox-rectangle">
                    <img 
                    v-if="isChecked"
                    src="@/assets/images/answerCheckMark.svg" alt="check mark">
                </div>
            </div>

            <div
            @click="selectCheckbox"
            :class="{'cursor-pointer': !isDisable}"
            class="answer-checkbox-text">
                {{answerCheckboxInfo.checkboxText}}
            </div>
      </div>

    </li>
</template>

<script>
export default {
    name: 'AnswerCheckbox',
    props: {
        answerCheckboxInfo: {},
        answerNumber: Number,
        isChecked: Boolean,
        isDisable: Boolean
    },
    methods: {
        selectCheckbox() {
            if (!this.isDisable) {
                this.$emit('selectCheckbox', this.answerNumber)
            }
        }        
    }
}

</script>

