<template>
    <div class="answers-buttons">   
        <AnswerHint
        :numberTry = actualProperty.numberTry
        />
        <ul class="answers-buttons-grid">
            
            <AnswerButton            
            v-for="(item,index) in actualProperty.buttonsList"
            :key="index"
            :answerText = actualView.bodyButtons[index].buttonText
            :isSelected = item
            :answerNumber = index            
            :isDisable = "actualProperty.completed"
            @selectButton = "selectButton($event)"
            />

        </ul>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
import AnswerButton from '@/components/game/AnswerButton.vue'
import AnswerHint from '@/components/game/AnswerHint.vue'

export default {
    name: "ManyAnswersButtons",
    components: {
        AnswerButton,
        AnswerHint
    },
    computed: {
      ...mapGetters([
          "actualView",
          'actualProperty',
          "stepNumber"
      ]),    
    },
    methods: {
        ...mapActions([
        "setCurrentAnswer",
        "setManyCurrentAnswer"
        ]),
        selectButton(answerNumber){            
            this.setManyCurrentAnswer({"number": answerNumber, "value": !this.actualProperty.buttonsList[answerNumber]})
        }        
    }
}
</script>

