<template>
    <button
      class="footer-button"
      @click="buttonClick"
    >
      <div>{{buttonData.name}}</div>
    
    </button>
</template>

<script>
export default {
    name: 'FooterButton',
    props: {
      buttonData: {}
    },
    computed: {
        
    },
    methods: {
      buttonClick(){
        this.$emit('buttonClick', this.buttonData.action)
      }
    }
}
</script>

