<template>
    <div class="footer-buttons">
        <FooterButton
        v-for="(item, index) in actualView.footerButtonList"
        :key="index"
        :buttonData = item
        @buttonClick = buttonClick($event)
        />

        <ModalWarning
        v-if="showModal"
        :correct = "correct"
        :warningText = "warningText"
        @closeModal = "closeModal"
        />

    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
import FooterButton from "@/components/game/FooterButton.vue"
import ModalWarning from "@/components/game/ModalWarning.vue"

export default {
    name: 'FooterButtons',
    components: {
        FooterButton,
        ModalWarning
    },
    data() {
        return {
            showModal: false,
            correct: false,
            warningText: ""
        }
    },
    computed: {
        ...mapGetters([
            "actualView",
            "actualProperty",
            "completed",
            "stepNumber",
            "testId"
        ])        
    },
    methods: {
        ...mapActions([
            "nextStep",
            "previousStep",
            "returnActualView",
            "setCompleted",
            "setCurrentAnswer",
            "setManyCurrentAnswer",
            "SEND_DATA_TO_API",
            "newStart",
            "increaseIndicator",
            "setTrueAnswers"
        ]),
        buttonClick(action){
            if (action === 'next') {
                if (this.completed) {
                    this.nextStep()
                }
                else {
                    if (this.actualView.type === "answersButtons") {
                        this.clickAnswersButtons()
                    }
                    if (this.actualView.type === "manyAnswersButtons") {
                        this.clickManyAnswersButtons()
                    }
                    else if (this.actualView.type === "answersCheckboxes") {
                        this.clickAnswersCheckboxes()
                    }
                    else if (this.actualView.type === "sections"){
                        this.clickSections()
                    }
                    else if (this.actualView.type === "info"){
                        this.clickInfo()                        
                    }
                    else if (this.actualView.type === "answers"){
                        this.clickAnswers()
                    }
                }
            }
            if (action === 'previous') {
                this.previousStep()
            }
            if (action === 'return') {
                this.returnActualView()
            }
            if (action === 'startAgain') {
                this.newStart()
            }
        },

        clickAnswersButtons(){
            if (this.actualProperty.currentAnswer !== null && this.actualView.bodyButtons[this.actualProperty.currentAnswer].isRight) {
                            
                this.SEND_DATA_TO_API(
                    {
                        "type": this.actualView.type,
                        "currentAnswer": this.actualProperty.currentAnswer,
                        "isRight": true,
                        "numberTry": this.actualProperty.numberTry
                    }
                )
                
                this.setCompleted(true)
                this.increaseIndicator(1)
                // this.openModal("correct", "Диагноз верный!", 1000, () =>{this.nextStep()})
                this.openModal("correct", "The diagnosis is correct!", 1000, () =>{this.nextStep()})
            }
            else {                        
                if (this.actualProperty.numberTry === 2) {
                    this.actualProperty.numberTry += 1
                    this.SEND_DATA_TO_API(
                        {
                            "type": this.actualView.type,
                            "currentAnswer": this.actualProperty.currentAnswer,
                            "isRight": false,
                            "numberTry": this.actualProperty.numberTry
                        }
                    )
                    this.setCompleted(true)
                    // this.openModal("notCorrect", "Диагноз не верный!", 1500, ()=>{
                    this.openModal("notCorrect", "The diagnosis is not correct!", 1500, ()=>{
                        // this.openModal("correct", "Даем подсказку!", 2000, ()=>{
                        this.openModal("correct", "We'll give you a hint!", 2000, ()=>{
                            this.setTrueAnswer()
                        })
                    })
                    this.increaseIndicator(1)
                }
                else if (this.actualProperty.numberTry < 2){
                    this.SEND_DATA_TO_API(
                        {
                            "type": this.actualView.type,
                            "currentAnswer": this.actualProperty.currentAnswer,
                            "isRight": false,
                            "numberTry": this.actualProperty.numberTry
                        }
                    )
                    this.actualProperty.numberTry += 1
                    // this.openModal("notCorrect", "Диагноз не верный!", 1500, ()=>{})
                    this.openModal("notCorrect", "The diagnosis is not correct!", 1500, ()=>{})
                }
            }
        },
        clickManyAnswersButtons(){
            let notAnswer = 0
            let rightAnswer = 0
            let correctness = ""
            
            for (let i = 0; i < this.actualView.bodyButtons.length; i++) {
                if (this.actualView.bodyButtons[i].isRight === false) {
                    if (this.actualProperty.buttonsList[i] === true) {
                        correctness = "notCorrect"
                        break
                    }
                }
                else if (this.actualView.bodyButtons[i].isRight === true) {
                    if (this.actualProperty.buttonsList[i] === false) {
                        notAnswer +=1
                    }
                    else {
                        rightAnswer +=1
                    }
                }
            }
            let correctText = ""
            if (correctness !== "notCorrect" && rightAnswer > 0 && notAnswer > 0) {
                correctness = "partlyCorrect"
                correctText = "Диагноз выставлен не полностью"
            }
            else if (correctness !== "notCorrect" && rightAnswer >0 && notAnswer === 0) {
                correctness = "correct"
                correctText = "Диагноз верный!"
            }
            else {
                correctness = "notCorrect"
                correctText = "Диагноз не верный!"
            }

            if (correctness === "correct") {

                this.SEND_DATA_TO_API(
                    {
                        "type": this.actualView.type,
                        "currentAnswer": this.actualProperty.buttonsList,
                        "isRight": true,
                        "numberTry": this.actualProperty.numberTry
                    }
                )
                
                this.setCompleted(true)
                this.increaseIndicator(1)
                this.openModal(correctness, correctText, 1000, () =>{this.nextStep()})
            }
            else {                        
                if (this.actualProperty.numberTry === 2) {
                    this.actualProperty.numberTry += 1
                    this.SEND_DATA_TO_API(
                        {
                            "type": this.actualView.type,
                            "currentAnswer": this.actualProperty.buttonsList,
                            "isRight": false,
                            "numberTry": this.actualProperty.numberTry
                        }
                    )
                    this.setCompleted(true)
                    this.openModal(correctness, correctText, 1500, ()=>{
                        this.openModal("correct", "Даем подсказку!", 2000, ()=>{
                            this.setManyTrueAnswer()
                        })
                    })
                    this.increaseIndicator(1)
                }
                else if (this.actualProperty.numberTry < 2){
                    this.SEND_DATA_TO_API(
                        {
                            "type": this.actualView.type,
                            "currentAnswer": this.actualProperty.buttonsList,
                            "isRight": false,
                            "numberTry": this.actualProperty.numberTry
                        }
                    )
                    this.actualProperty.numberTry += 1
                    this.openModal(correctness, correctText, 1500, ()=>{})                                
                }
            }
        },
        clickAnswersCheckboxes(){
            for (let i = 0; i < this.actualView.bodyCheckboxes.length; i++) {
                if (!this.actualView.bodyCheckboxes[i].notRequired) {
                    if (this.actualView.bodyCheckboxes[i].isRight !== this.actualProperty.checkboxList[i]) {                            
                        
                        if (this.actualProperty.numberTry === 2) {
                            this.actualProperty.numberTry += 1
                            this.SEND_DATA_TO_API(
                            {
                                "type": this.actualView.type,
                                "currentAnswer": this.actualProperty.checkboxList,
                                "isRight": false,
                                "numberTry": this.actualProperty.numberTry
                            }
                            )
                            
                            if (this.actualView.checkboxHints !== undefined) {
                                let answersAndRequired = []
                                for (let k = 0; k < this.actualProperty.checkboxList.length; k++) {
                                    if (this.actualView.bodyCheckboxes[k].notRequired) {
                                        answersAndRequired.push(true)
                                    }
                                    else {
                                        answersAndRequired.push(this.actualProperty.checkboxList[k])
                                    }                                                
                                }

                                for (let j = 0; j < this.actualView.checkboxHints.length; j++) {                                                
                                    
                                    let a = answersAndRequired.toString();
                                    let b = this.actualView.checkboxHints[j].answersForHint.toString();

                                    let is_same = false
                                    if (a===b) {
                                        is_same = true
                                    }

                                    if (is_same) {

                                        this.openModal("partlyCorrect", this.actualView.checkboxHints[j].hintText, 2500, ()=>{
                                            // this.openModal("correct", "Даем подсказку!", 2000, ()=>{
                                            this.openModal("correct", "We'll give you a hint!", 2000, ()=>{
                                                this.setTrueCheckboxes(() => {this.setCompleted(true)})
                                        })})

                                        break
                                    }
                                    else{
                                        // this.openModal("notCorrect", "Выбранное лечение не верное!", 1500, ()=>{
                                        this.openModal("notCorrect", "The chosen treatment is not correct!", 1500, ()=>{
                                            // this.openModal("correct", "Даем подсказку!", 2000, ()=>{
                                            this.openModal("correct", "We'll give you a hint!", 2000, ()=>{
                                                this.setTrueCheckboxes(() => {this.setCompleted(true)})
                                        })})
                                    }
                                }                                            

                            }
                            else {
                                // this.openModal("notCorrect", "Выбранное лечение не верное!", 1500, ()=>{
                                this.openModal("notCorrect", "The chosen treatment is not correct!", 1500, ()=>{
                                    // this.openModal("correct", "Даем подсказку!", 2000, ()=>{
                                    this.openModal("correct", "We'll give you a hint!", 2000, ()=>{
                                        this.setTrueCheckboxes(() => {this.setCompleted(true)})
                                })})
                            }

                            this.increaseIndicator(1)
                            return
                        }
                        else if (this.actualProperty.numberTry < 2){
                            this.actualProperty.numberTry += 1

                            this.SEND_DATA_TO_API(
                            {
                                "type": this.actualView.type,
                                "currentAnswer": this.actualProperty.checkboxList,
                                "isRight": false,
                                "numberTry": this.actualProperty.numberTry
                            }
                            )

                            let warningCorrectText = ""
                            if (this.actualView.warningCorrectText) {
                                warningCorrectText = this.actualView.warningCorrectText
                            }
                            else{
                                // warningCorrectText = "Скорректируйте лечение!"
                                warningCorrectText = "Adjust your treatment!"
                            }
                            
                            if (this.actualView.checkboxHints !== undefined) {
                                let answersAndRequired = []
                                for (let k = 0; k < this.actualProperty.checkboxList.length; k++) {
                                    if (this.actualView.bodyCheckboxes[k].notRequired) {
                                        answersAndRequired.push(true)
                                    }
                                    else {
                                        answersAndRequired.push(this.actualProperty.checkboxList[k])
                                    }                                                
                                }

                                for (let j = 0; j < this.actualView.checkboxHints.length; j++) {                                                
                                    
                                    let a = answersAndRequired.toString();
                                    let b = this.actualView.checkboxHints[j].answersForHint.toString();

                                    let is_same = false
                                    if (a===b) {
                                        is_same = true
                                    }

                                    if (is_same) {
                                        this.openModal("partlyCorrect", this.actualView.checkboxHints[j].hintText, 2500, ()=>{})
                                        break
                                    }
                                    else{
                                        this.openModal("notCorrect", warningCorrectText, 1500, ()=>{})
                                    }
                                }                                            

                            }
                            else {
                                this.openModal("notCorrect", warningCorrectText, 1500, ()=>{})
                            }

                            return
                        }                                
                    }
                }
            }
            
            this.SEND_DATA_TO_API(
                {
                    "type": this.actualView.type,
                    "currentAnswer": this.actualProperty.checkboxList,
                    "isRight": true,
                    "numberTry": this.actualProperty.numberTry
                }
            )
            
            this.setCompleted(true)
            this.increaseIndicator(1)
            // this.openModal("correct", "Лечение верное!", 1000, () =>{this.nextStep()})
            this.openModal("correct", "The treatment is correct!", 1000, () =>{this.nextStep()})
        },
        clickSections(){
            for (let i = 0; i < this.actualProperty.viewedSections.length; i++) {
                if (!this.actualProperty.viewedSections[i]) {
                    // this.openModal("notCorrect", "Осмотр проведен не полностью", 1000, ()=>{})
                    this.openModal("notCorrect", "The inspection was not completed", 1000, ()=>{})
                    return
                }
            }
            this.SEND_DATA_TO_API(
                {
                    "type": this.actualView.type
                }
            )

            this.setCompleted(true)
            this.increaseIndicator(1)
            this.nextStep()
        },
        clickInfo(){
            if ((this.actualView.acceptCheckbox) && (this.actualProperty.acceptCheckbox === false)) {
                // this.openModal("notCorrect", "Поставьте галочку в графе “Принимаю”, чтобы продолжить", 2000, ()=>{})
                this.openModal("notCorrect", "Tick the “I Accept” box to continue", 2000, ()=>{})
                return
            }
            this.SEND_DATA_TO_API(
                {
                    "type": this.actualView.type
                }
            )

            this.setCompleted(true)
            this.increaseIndicator(1)
            this.nextStep()
        },
        clickAnswers(){
            const answerStatus = this.compareAnswers()

            let warningText = ""
            if (answerStatus==="correct") {
                warningText = this.actualView.correctTextDefault
            }
            if (answerStatus==="partlyCorrect") {
                warningText = this.actualView.partlyCorrectTextDefault
            }
            if (answerStatus==="notCorrect") {
                warningText = this.actualView.notCorrectTextDefault
            }

            if (this.actualView.answerWarnings.length > 0) {
                let answersTextList = ""
                for (let i = 0; i < this.actualProperty.answersList.length; i++) {
                    if (this.actualProperty.answersList[i] === true) {
                        answersTextList += i+1 +","
                    }
                }
                answersTextList = answersTextList.slice(0, -1); 

                for (let i = 0; i < this.actualView.answerWarnings.length; i++) {
                    if (this.actualView.answerWarnings[i].answersForWarning === answersTextList) {
                        warningText = this.actualView.answerWarnings[i].warningText
                    }
                    
                }
            }            

            if (answerStatus==="correct") {
                this.SEND_DATA_TO_API(
                    {
                        "type": this.actualView.type,
                        "currentAnswer": this.actualProperty.answersList,
                        "isRight": true,
                        "numberTry": this.actualProperty.numberTry
                    }
                )
                
                this.setCompleted(true)
                this.increaseIndicator(1)
                this.openModal(answerStatus, warningText, 1000, () =>{this.nextStep()})
                return
            }            
                    
            if (this.actualProperty.numberTry === this.actualView.totalHints-1) {
                this.actualProperty.numberTry += 1
                this.SEND_DATA_TO_API(
                    {
                        "type": this.actualView.type,
                        "currentAnswer": this.actualProperty.buttonsList,
                        "isRight": false,
                        "numberTry": this.actualProperty.numberTry
                    }
                )
                this.setCompleted(true)
                this.openModal(answerStatus, warningText, 1500, ()=>{
                    this.openModal("correct", "Даем подсказку!", 2000, ()=>{
                        this.setTrueAnswers()
                    })
                })
                this.increaseIndicator(1)
            }
            else if (this.actualProperty.numberTry < this.actualView.totalHints-1){
                this.SEND_DATA_TO_API(
                    {
                        "type": this.actualView.type,
                        "currentAnswer": this.actualProperty.buttonsList,
                        "isRight": false,
                        "numberTry": this.actualProperty.numberTry
                    }
                )
                this.actualProperty.numberTry += 1
                this.openModal(answerStatus, warningText, 1500, ()=>{})                                
            }            
        },

        compareAnswers(){
            let notAnswer = 0
            let rightAnswer = 0
            
            for (let i = 0; i < this.actualView.bodyAnswers.length; i++) {
                if (this.actualView.bodyAnswers[i].notRequired) {
                    continue
                }
                if (this.actualView.bodyAnswers[i].isRight === false) {
                    if (this.actualProperty.answersList[i] === true) {
                        return "notCorrect"
                    }
                }
                else if (this.actualView.bodyAnswers[i].isRight === true) {
                    if (this.actualProperty.answersList[i] === false) {
                        notAnswer +=1
                    }
                    else {
                        rightAnswer +=1
                    }
                }
            }
            if (rightAnswer > 0 && notAnswer > 0) {
                return "partlyCorrect"
            }
            else if (rightAnswer >0 && notAnswer === 0) {
                return "correct"
            }
            else {
                return "notCorrect"
            }
        },
        

        async openModal(correct, warningText, showTime, callback){
            this.correct = correct
            this.showModal = true
            this.warningText = warningText
            await this.sleep(showTime)
            this.showModal = false
            callback()
        },
        closeModal(){
            this.showModal = false
        },

        setTrueAnswer(){
            for (let i = 0; i < this.actualView.bodyButtons.length; i++) {
                if (this.actualView.bodyButtons[i].isRight) {
                    this.setCurrentAnswer(i)
                }
            }
        },
        setManyTrueAnswer(){
            for (let i = 0; i < this.actualView.bodyButtons.length; i++) {
                    this.setManyCurrentAnswer({"number": i, "value": this.actualView.bodyButtons[i].isRight})
            }
        },

        async setTrueCheckboxes(callback){
            for (let i = 0; i < this.actualView.bodyCheckboxes.length; i++) {
                this.actualProperty.checkboxList[i] = this.actualView.bodyCheckboxes[i].isRight
            }
            callback()
        },

        sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms));
        }       
        
    }
    
}
</script>

