<template>
    <li class="answer-button"
    :class="{
          'answer-button-selected': isSelected,
          'answer-button-not-selected': !isSelected,

        }"
    @click="buttonClick"
    >
       
        <div class="answer-button-text">
            {{answerText}}
        </div>

    </li>
</template>

<script>
export default {
    name: "AnswerButton",
    components: {

    },
    props: {
        answerText: String,
        isSelected: Boolean,
        answerNumber: Number,
        notOneButtonSelected: Boolean,
        isDisable: Boolean
    },
    methods: {
        buttonClick(){
            if (!this.isDisable) {
                this.$emit("selectButton", this.answerNumber)
            }
        }
    }

}
</script>

