<template>
    <div class="game-checkboxes">
        <div class="answers-checkboxes">
            <h2 class="answers-checkboxes-title">
                {{actualView.headerText}}
            </h2>

            <AnswerHint
            :numberTry = actualProperty.numberTry
            />

            <ul class="answers-checkboxes-grid">
                <AnswerCheckbox 
                v-for="(item,index) in actualView.bodyCheckboxes"
                :key="index"
                :answerCheckboxInfo = item
                :isChecked = actualProperty.checkboxList[index]
                :answerNumber = index
                :isDisable = actualProperty.completed
                @selectCheckbox = selectCheckbox(index)
                />
            </ul>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
import AnswerCheckbox from '@/components/game/AnswerCheckbox.vue'
import AnswerHint from '@/components/game/AnswerHint.vue'

export default {
    name: 'AnswersCheckboxes',
    components: {
        AnswerCheckbox,
        AnswerHint
    },
    computed: {
      ...mapGetters([
          "actualView",
          'actualProperty',
          "stepNumber"
      ])      
    },
    methods: {
        ...mapActions([
        "changeAnswerCheckbox"
        ]),
        selectCheckbox(index){
            this.changeAnswerCheckbox(index)     
        }
    }
}
</script>

