<template>
    <li>
        <div class="answer-checkbox">
            <div>
                <div 
                :class="{'white-checkbox': !isSelected,
                        'purple-checkbox': isSelected,
                        'cursor-pointer': !isDisable
                }"
                class="answer-checkbox-rectangle">
                    <img 
                    v-if="isSelected"
                    src="@/assets/images/answerCheckMark.svg" alt="check mark">
                </div>
            </div>

            <div
            :class="{'cursor-pointer': !isDisable}"
            class="answer-checkbox-text">
                {{answerText}}
            </div>
      </div>
    </li>
</template>

<script>
export default {
    name: 'UniversalAnswerCheckbox',
    props: {
        isSelected: Boolean,
        isDisable: Boolean,
        answerText: String
    }
}

</script>

