<template>
    <div class="answers-buttons">   
        <AnswerHint
        :numberTry = actualProperty.numberTry
        />
        <ul class="answers-buttons-grid">
            
            <AnswerButton            
            v-for="(item,index) in actualView.bodyButtons"
            :key="index"
            :answerText = item.buttonText
            :isSelected = isSelected(index)
            :answerNumber = index            
            :isDisable = "actualProperty.completed"
            @selectButton = "selectButton($event)"
            />

        </ul>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
import AnswerButton from '@/components/game/AnswerButton.vue'
import AnswerHint from '@/components/game/AnswerHint.vue'

export default {
    name: "AnswersButtons",
    components: {
        AnswerButton,
        AnswerHint
    },
    computed: {
      ...mapGetters([
          "actualView",
          'actualProperty',
          "stepNumber"
      ])
    },
    methods: {
        ...mapActions([
        "setCurrentAnswer"
        ]),
        selectButton(answerNumber){            
            this.setCurrentAnswer(answerNumber)
        },
        isSelected(index){
            if (this.actualProperty.currentAnswer === index) {
                return true
            }
            else {
                return false
            }        
        }
    }
}
</script>

